import React from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { Button, Descriptions, Tabs, } from 'antd';
import NumberFormat from 'react-number-format';
import { ADD_TO_CART_REQUEST } from '../../../reducers/types'

function ProductInfo() {

  const dispatch = useDispatch();
  const { currentProduct } = useSelector(state => state.jaymall)

  const handleAddToCart = () => {
    dispatch({
      type: ADD_TO_CART_REQUEST,
      payload: { productId: currentProduct._id }
    })
  }

  return (
    <div>
      <Tabs defaultActiveKey="1">
        <Tabs.TabPane tab=" " key="1">
          <Descriptions >
            <Descriptions.Item label="" span={3}>
              <NumberFormat value={currentProduct.price} displayType={'text'} thousandSeparator={true} suffix={''} />
            </Descriptions.Item>
            <Descriptions.Item label="" span={3}>{currentProduct.views}</Descriptions.Item>
            <Descriptions.Item label="" span={3}>{currentProduct.sold}</Descriptions.Item>
            <Descriptions.Item >&nbsp;&nbsp;{currentProduct.description}</Descriptions.Item>
          </Descriptions>
          <div style={{ display: 'flex', justifyContent: 'center', marginTop: 40 }}>
            <Button
              size='large'
              shape='round'
              type='danger'
              onClick={handleAddToCart}
            > </Button>
          </div>
        </Tabs.TabPane>

        <Tabs.TabPane tab=" " key="2">
           
        </Tabs.TabPane>
        <Tabs.TabPane tab=" " key="3">
           
        </Tabs.TabPane>
      </Tabs>

    </div>
  )
}

export default ProductInfo
