import React from 'react';
import { useSelector } from 'react-redux';
import StyledLink from '../../utils/StyledLink'
import { Menu, Badge } from 'antd';
import styled from 'styled-components'

const Title = styled.span`
  margin: 0 5px
`

function LeftMenu(props) {

  const { currentUser } = useSelector(state => state.user)

  return (
    <Menu mode={props.mode}>
      <Menu.Item><StyledLink to='/'>HOME</StyledLink></Menu.Item>
      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="blog" >
          {<StyledLink to='/blog'></StyledLink>}
        </Menu.Item>
        <Menu.Item key="blog/create" disabled={!currentUser?.isAuth} >
          {<StyledLink to='/blog/create'></StyledLink>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="movieTrend">
          {<StyledLink to='/movieTrend'></StyledLink>}
        </Menu.Item>
        <Menu.Item key="movieTrend/favorite" disabled={!currentUser?.isAuth} >
          {<StyledLink to='/movieTrend/favorite'></StyledLink>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="talkative" disabled={!currentUser?.isAuth} >
          {<StyledLink to='/talkative'></StyledLink>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu
        title={currentUser?.cart?.length
          ? <Badge dot showZero={false} offset={[0, 0]}>
            <Title></Title>
          </Badge>
          : <Title></Title>}
      >
        <Menu.Item key="jaymall">
          {<StyledLink to='/jaymall'></StyledLink>}
        </Menu.Item>
        <Menu.Item key="cart" disabled={!currentUser?.isAuth} >
          <StyledLink to='/jaymall/cart'>
            <Badge size='small' count={currentUser?.cart?.length} showZero={false} offset={[0, -10]} />
          </StyledLink>
        </Menu.Item>
        <Menu.Item key="product" disabled={!currentUser?.isAuth} >
          {<StyledLink to='/jaymall/upload'></StyledLink>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="jaytube" disabled>
          {<StyledLink to='/jaytube'></StyledLink>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="jaytweet" disabled>
          {<StyledLink to='/jaytweet'></StyledLink>}
        </Menu.Item>
      </Menu.SubMenu>
    </Menu>
  )
}

export default LeftMenu
